import React, {Component} from "react";
import {PropTypes} from "prop-types";
import {OraSimpleRadio} from "eshop/components/OraCommonComponents";
import OnlineUtils from "eshop/utils/OnlineUtils";


class MsisdnPickerComponent extends Component  {
    constructor (props) {
        super(props);
    }

    componentDidMount(){
        if(this.props.fetch) {
            this.props.fetchMsisdns();
        }
    }

    componentWillReceiveProps (nextProps) {
        if(nextProps.fetch && !this.props.fetch) {
            this.props.fetchMsisdns();
        }
    }

    renderList() {
        let name;
        let transformedMsisdns = [...this.props.msisdns.filter(it => it.number === this.props.entry.msisdn),
                                  ...this.props.msisdns.filter(it => it.number !== this.props.entry.msisdn).sort((a, b) => a.status < b.status ? -1 : (a.status > b.status ? 1 : 0))
                                 ];
        return transformedMsisdns.map(function (el, k) {
            name = "msisdn";
            return <li className="l-col l-col-small-6 l-col-medium-6 l-col-6 u-padding-l" key={el.number}>
                        <OraSimpleRadio name={name} className="o-radio opl-radio" value={el.number} onChange={this.props.changeMsisdn.bind(this, el.number)}
                                        checked={this.props.msisdn == el.number} text={OnlineUtils.transformMsisdn(el.number)} readOnly={el.status === "LOCAL" && el.number !== this.props.entry.msisdn}></OraSimpleRadio>
                    </li>
        }.bind(this));
    }

    render() {
        return (
            <div className="l-row u-margin-top-l">
                <div className="l-col l-col-12">
                    <h3 className="h4 u-margin-l">
                        Zmie≈Ñ numer
                    </h3>
                    <fieldset>
                        <legend className="u-acc-hide">wybierz numer z listy</legend>
                        <ul className="l-row">
                            {this.renderList()}
                        </ul>
                    </fieldset>
                </div>
            </div>
        )
    }
}

MsisdnPickerComponent.defaultProps = {
    msisdns: []
}

MsisdnPickerComponent.propTypes = {
      msisdn: PropTypes.string,
      msisdns: PropTypes.arrayOf(
             PropTypes.shape({
                 number: PropTypes.string,
                 status: PropTypes.string
             })
          ),
      fetchMsisdns: PropTypes.func,
      changeMsisdn: PropTypes.func,
      fetch: PropTypes.bool
}

export default MsisdnPickerComponent;
